<template>
  <div class="item-list" :class="{ 'collect-status': collectStatus }">
    <floatLayer :show="layerShow" :item="nowDetail" :urlType="4" @parentEvent="hideDetail" />
    <div class="ctrl-panel">
      <div class="switch-box" :class="{ active: collectFilter }" @click="getCollectFilter">
        <div class="switch-label"></div>
        <div class="switch-btn"></div>
      </div>
      <div class="btn" v-text="collectStatus ? '' : ''" @click="collectToggle"></div>
      <tagBar :tags="tags" @parentEvent="getDataByTag" />
    </div>
    <propItem v-for="hk in nowList" v-bind:item="hk" :class="{ collect: getCollect(hk.id) }" :urlType="4"
      :showPic="true" :key="hk.id" :defaultColor="'#5420ae'" :barColor="hk.color" @click.native="showDetail(hk)"
      v-show="collectFilter == false || !getCollect(hk.id)" />
  </div>
</template>

<script>
import PropItem from "@/components/PropItem";
import TagBar from "@/components/TagBar";
import FloatLayer from "@/components/FloatLayer";
export default {
  name: "Decker",
  components: {
    PropItem,
    TagBar,
    FloatLayer,
  },
  data() {
    return {
      tags: [
        {
          name: "ALL",
          title: "",
          count: 0,
          own: 0,
        }, {
          name: "XD",
          title: "",
          count: 0,
          own: 0,
        },
        {
          name: "DX",
          title: "DX",
          count: 0,
          own: 0,
        },
        {
          name: "DX01",
          title: "DX01",
          count: 0,
          own: 0,
        },
        {
          name: "DX02",
          title: "DX02",
          count: 0,
          own: 0,
        },
      ],
      nowList: null, //
      nowDetail: null,
      layerShow: false,
      collectStatus: false, //
      collectFilter: false, //
      collectList: [], //
      dataList: [
        {
          id: "D-1",
          type: "U",
          tag: ["DX"],
          name: " ",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-1.jpg",
        },
        {
          id: "D-2",
          type: "U",
          tag: ["DX"],
          name: " ",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-2.jpg",
        },
        {
          id: "D-3",
          type: "U",
          tag: ["DX02"],
          name: " ",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-3.jpg",
        },
        {
          id: "D-4",
          type: "U",
          tag: ["DX01"],
          name: "",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-4.jpg",
        },
        {
          id: "D-5",
          type: "U",
          tag: ["DX01"],
          name: " ",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-5.jpg",
        },
        {
          id: "D-6",
          type: "U",
          tag: ["DX01"],
          name: "",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-6.jpg",
        },
        {
          id: "D-7",
          type: "U",
          tag: ["DX02"],
          name: "",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-7.jpg",
        },
        {
          id: "D-8",
          type: "U",
          tag: ["DX02"],
          name: "",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-8.jpg",
        },
        {
          id: "D-9",
          type: "U",
          tag: ["DX02"],
          name: "",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-9.jpg",
        },
        {
          id: "D-10",
          type: "U",
          tag: ["DX02"],
          name: "",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-10.jpg",
        },
        {
          id: "D-11",
          type: "U",
          tag: ["DX"],
          name: "",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-11.jpg",
        },
        {
          id: "D-12",
          type: "U",
          tag: ["DX"],
          name: "12",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-12.jpg",
        },
        {
          id: "D-13",
          type: "U",
          tag: ["DX"],
          name: "2 ",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-13.jpg",
        },
        {
          id: "D-14",
          type: "U",
          tag: ["DX01"],
          name: "1 ",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-14.jpg",
        },
        {
          id: "D-15",
          type: "U",
          tag: ["DX01"],
          name: "15",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-15.jpg",
        },
        {
          id: "D-16",
          type: "U",
          tag: ["DX01"],
          name: "16",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-16.jpg",
        },
        {
          id: "D-17",
          type: "U",
          tag: ["DX02"],
          name: "17",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-17.jpg",
        },
        {
          id: "D-18",
          type: "U",
          tag: ["XD"],
          name: "",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-18.jpg",
        },
        {
          id: "D-19",
          type: "U",
          tag: ["XD"],
          name: "",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-19.jpg",
        },
        {
          id: "D-20",
          type: "U",
          tag: ["XD"],
          name: "",
          link: null,
          color: "#018dd1",
          way: "",
          pic: "D-20.jpg",
        },
      ],
    };
  },
  methods: {
    getDataByTag(tag) {
      if (tag == "ALL") {
        this.nowList = this.dataList;
      } else {
        this.nowList = this.getListByTag(tag);
      }
    },
    // tag
    getListByTag(tag) {
      let arr = [];
      this.dataList.forEach((item, index, array) => {
        if (item.tag.indexOf(tag) != -1) {
          arr.push(item);
        }
      });
      return arr;
    },
    // collectStatus,
    showDetail(item) {
      if (this.collectStatus) {
        // 
        if (this.collectList.indexOf(item.id) == -1) {
          this.collectList.push(item.id); //
        } else {
          this.collectList.splice(this.collectList.indexOf(item.id), 1); //
        }
        this.$Tools.$setStroage("decker", this.collectList); //
        this.getCollectProgress();
      } else {
        // 
        this.layerShow = true;
        this.nowDetail = item;
      }
    },
    // 
    hideDetail(visible) {
      this.layerShow = visible;
    },
    // 
    getCollectData() {
      this.collectList =
        this.$Tools.$getStroage("decker") != null
          ? this.$Tools.$getStroage("decker")
          : [];
    },
    // 
    collectToggle() {
      this.collectStatus = this.collectStatus ? false : true;
    },
    // 
    getCollect(id) {
      return this.collectList.indexOf(id) != -1; //true
    },
    //
    getCollectFilter() {
      this.collectFilter = this.collectFilter ? false : true;
    },
    // 
    //  !=-1
    getCollectProgress() {
      this.tags.forEach((i, num, arr) => {
        this.tags[num].count = 0;
        this.tags[num].own = 0;
      });
      this.tags[0].count = this.dataList.length;
      this.tags[0].own = this.collectList.length;
      this.dataList.forEach((item, index, array) => {
        this.tags.forEach((i, num, arr) => {
          //
          if (item.tag[0].indexOf(i.name) != -1) {
            this.tags[num].count = this.tags[num].count + 1;
          }
          //
          if (
            this.collectList.indexOf(item.id) != -1 &&
            this.tags[num].name == item.tag[0]
          ) {
            this.tags[num].own = this.tags[num].own + 1;
          }
        });
      });
    },
  },
  created() {
    this.nowList = this.dataList;
    this.getCollectData();
    this.getCollectProgress();
  },
};
</script>

<style scoped>
</style>



